html, body {
  margin: 0px;
  padding: 0px;
  width: 100%;
  height: 99%;
  font-size: 16px;
}

html, html *, html *:before, html *:after {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

body {
  font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
  background: #ccc;
  background: -webkit-radial-gradient(center, circle, rgba(210,210,210,1) 0%,rgba(255,255,255,1) 100%);
  background: -o-radial-gradient(center, circle, rgba(210,210,210,1) 0%,rgba(255,255,255,1) 100%);
  background: radial-gradient(circle at center, rgba(210,210,210,1) 0%,rgba(255,255,255,1) 100%); 
  padding: 20px;
}

h1 {
  margin: 20px;
  font-weight: normal;
}

button {
  font-size: 16px;
}

p {
  margin: 0px;
}

a {
  font-weight: bold;
  -webkit-transition: opacity 300ms ease;
  -o-transition: opacity 300ms ease;
  transition: opacity 300ms ease;
  display: inline-block;
}
  
a:hover {
  opacity: 0.7;
}



